.eduBlock{
  display: grid;
  margin: 1rem 0;
  grid-template-columns: 4rem 1fr 4rem;
  grid-template-areas:
  '. schoolName edit'
  '. schoolName edit'
  '. FOS edit'
  '. FOS delete'
  '. date delete' 
  '. date delete'; 
}

.schoolName{
  grid-area: schoolName;
  font-weight: bold;
  text-align: left;
}

.FOS{
  grid-area: FOS;
  text-align: left;
}

.schoolDate{
  grid-area: date;
  text-align: left;
}

.eduBlock>button.editButton{
  grid-area: edit;
  align-self: flex-start;
  justify-self: center;
}

.eduBlock>button.deleteButton{
  grid-area: delete;
  align-self: flex-end;
  justify-self: center;
}